.App-header {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .logo {
        .logo-img {
            width: 100%;
            height: 100%;
            svg {
                width: 170px;
                height: 65px;
            }
        }
    }
    .nav-header {
        width: 50%;
        display: flex;
        gap: 30px;
        align-items: center;
        justify-content: right;
    }
    .nav-item {
        font-size: 20px;
        font-weight: 500;
        a {
            font-size: inherit;
            font-weight: inherit;
        }
        a:hover {
            text-decoration: underline;
        }
    }
    .active {
        color: #450fce;
        text-decoration: underline;
    }
}
