@import "..//../variables/variables.scss";

.characters-block {
    width: 74%;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    display: flex;
    gap: 30px;
}

.character-card {
    height: 450px;
    max-width: 300px;
    padding: 15px;
    border-radius: 5px;
    box-shadow: $whiteboxshadow;
    background-color: #fefefe;
    scale: 1;
}
.character-card:hover {
    box-shadow: $blackboxshadow;
}
.character-card.active {
    scale: 1.03;
    box-shadow: $blackboxshadow;
}

.character-img {
    width: 270px;
    height: 270px;
    border-radius: 5px;
}
.character-name {
    margin-top: 10px;
    text-align: center;
}

@keyframes col {
    from {
        scale: 0;
    }
    to {
        scale: 1;
    }
}
.button.topage {
    animation: col 0.7s ease;
    display: block;
    margin: 20px auto;
    scale: 1;
}

.button:hover {
    border: 1px solid black;
}

.button:active {
    background-color: #e38080;
}

.linkto {
    margin: 0 auto;
    display: block;
    width: 125px;
}
